.Popup {
  display: flex;
  justify-content: space-between;
  background-color: #1f1f1f;
  width: 95%;
  height: auto;
  position: fixed;
  z-index: 1;
  margin: auto;
  top: 21%;
  color: #fff;
  left: 2.4%;
  border-radius: 3px; }
  .Popup .Popup--info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px 20px 0 20px;
    letter-spacing: 1px;
    font-size: 20px;
    font-weight: 300; }
  .Popup .Popup--facts {
    padding: 15px; }
  .Popup .Popup--author-pub {
    padding: 4% 0%; }
  .Popup .Popup--title {
    font-size: 1.5em;
    padding-top: 2%; }
  .Popup .Popup--image {
    width: 50%;
    padding: 10px 10px 10px 0;
    border-radius: 2px; }
  .Popup .Popup--star {
    position: absolute;
    top: 2%;
    background-color: #1f1f1fd1;
    padding: 10px 12px;
    border-radius: 50%;
    left: 94%;
    cursor: pointer;
    transition: all 500ms ease;
    margin-top: 5px; }
    .Popup .Popup--star:hover {
      background-color: #424242; }
  .Popup .Popup--close {
    position: absolute;
    background-color: #1f1f1fd1;
    top: 2%;
    padding: 10px 12px;
    border-radius: 50%;
    left: 88%;
    cursor: pointer;
    color: #000;
    transition: all 500ms ease;
    margin-top: 5px; }
    .Popup .Popup--close:hover {
      background-color: #424242; }

.Popup--buttons {
  margin-left: auto;
  padding-top: 20px; }

.Popup--button,
.CopyToClipboard--button {
  background-color: #727272;
  color: #fff;
  border: none;
  width: 130px;
  height: 42px;
  border-radius: 20px 20px 0px 0px;
  letter-spacing: 4px;
  font-weight: 500;
  font-size: 18px;
  margin-right: 20px;
  cursor: pointer;
  transition: all 500ms ease; }
  .Popup--button:hover,
  .CopyToClipboard--button:hover {
    background-color: #424242; }
  .Popup--button:focus,
  .CopyToClipboard--button:focus {
    outline: none; }

@media screen and (max-width: 1100px) {
  .Popup {
    flex-direction: column;
    display: flex;
    top: 5%;
    left: 1.4%;
    width: 97%;
    height: auto; }
    .Popup .Popup--info {
      overflow: scroll;
      font-size: 16px;
      padding-top: 0; }
    .Popup .Popup--author-pub {
      padding: 1% 0%; }
    .Popup .Popup--facts {
      padding: 0px; }
    .Popup .Popup--title {
      font-size: 1.6em;
      padding: 2% 0;
      text-align: center; }
    .Popup .Popup--delete-fav {
      display: flex;
      justify-content: flex-end;
      padding: 10px 10px 0 0; }
      .Popup .Popup--delete-fav .Popup--star {
        position: unset;
        background-color: #4a4949; }
        .Popup .Popup--delete-fav .Popup--star:hover {
          background-color: #373636; }
      .Popup .Popup--delete-fav .Popup--close {
        position: unset;
        background-color: #4a4949;
        margin-right: 8px; }
        .Popup .Popup--delete-fav .Popup--close:hover {
          background-color: #373636; }
    .Popup .Popup--buttons {
      text-align: center;
      margin: 0; }
    .Popup .Popup--image {
      width: 90%;
      margin: 10px auto;
      max-height: 550px; }
    .Popup .Popup--button,
    .Popup .CopyToClipboard--button {
      border-radius: 8px;
      font-size: 13px;
      width: 35%;
      height: 30px;
      margin-top: 10px;
      font-weight: 400; } }

@media screen and (max-width: 600px) {
  .Popup {
    top: 15%; }
    .Popup .Popup--image {
      width: 75%; }
    .Popup .Popup--author-pub,
    .Popup .Popup--facts {
      font-size: 12px; } }

@media screen and (max-width: 480px) {
  .Popup .Popup--image {
    height: 45%; } }

